.container{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    position: absolute;
}
.calculator{
    padding: 20px;
    border-radius: 10px;
    background-color: rgb(246, 239, 44);
    border: 2px solid saddlebrown;
}
form input{
    border: none;
    outline: 0;
    width: 60px;
    height: 60px;
    font-size: 16px;
   
    margin: 2px;
    border-radius: 10px;
    border: 2px solid saddlebrown;
    color: black;
    font-weight: bolder;
    cursor: pointer;
}
form input[type="button"]:hover{
    background-color: rgb(249, 249, 139);
}

 form .display{
        display: flex;
        justify-content: flex-end;
        margin: 5px 0px 15px 0px;

}


form .display input{
    text-align: right;
    flex: 1;
    font-size: 40px;
    padding: 5px 10px;
    background-color: rgb(111, 142, 118);
}
.robot img {
    max-width: 100%;
    max-height: 100%;
    width: 20vw; /* Adjust the width based on the viewport width */
    height: auto;
}

form input.equal{
    width: 123px;
}
 